.page-header {
  font-family: "Open Sans Bold", "Arial", sans-serif;
  background-color: @deepblue;
  color: @white;
  background-image: url("../../img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    display: grid;
    grid-template-rows: min-content 1fr;
    min-height: 110px;
    background-image: url("../../img/back tablet.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  @media (min-width: @desktop-width) {
    min-height: 853px;
    display: grid;
    grid-template-rows: min-content 1fr;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.1);
    background-image: url("../../img/back-desktop.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.page-header--small {
  @media (min-width: @desktop-width) {
    min-height: 253px;
  }
}

.page-header__wrapper {
  @media (min-width: @desktop-width) {
    display: grid;
    width: 940px;
    margin: 0 auto;
    grid-template-columns: 278px 662px;
  }
}

.page-header__logo-wrapper {
  padding: 23px 0 0 20px;
  min-height: 70px;
  border-bottom: 1px solid @black;

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    min-height: 109px;
    padding: 44px 0 0 92px;
  }

  @media (min-width: @desktop-width) {
    width: 147px;
    text-align: center;
    margin: 0 0 0 0;
    padding: 44px 0 0 71px;
    border: none;
  }
}

.page-header__container {
  background-color: @deepblue;

  @media (min-width: @desktop-width) {
    display: block;
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
}
